import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { TeacherDashComponent } from './teacher-dash.component';
import { NotesComponent } from './notes/notes.component';
import { AssignmentsComponent } from './assignments/assignments.component';
import { QuizzesComponent } from './quizzes/quizzes.component';
import { ConsultComponent } from './consult/consult.component';
import { AnalyticsComponent } from './analytics/analytics.component';
import { CKEditorModule } from '@ckeditor/ckeditor5-angular';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: TeacherDashComponent,
    children: [{
      path: 'notes',
      component: NotesComponent
    },
    {
      path: 'assignments',
      component: AssignmentsComponent
    },

    {
      path: 'consult',
      component: ConsultComponent
    },
    {
      path: 'quizzes',
      component: QuizzesComponent
    },
    {
      path: 'analytics',
      component: AnalyticsComponent
    },
  ]
  },


]
@NgModule({
  imports: [
    CommonModule,
    CKEditorModule,
    Ng2SmartTableModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule
  ],
  exports: [],
  declarations: [TeacherDashComponent, NotesComponent, AssignmentsComponent, QuizzesComponent, ConsultComponent, AnalyticsComponent],
  providers: [DatePipe],
})
export class TeacherDashModule { }
