import { Component, OnInit } from "@angular/core";
import {DatePipe} from "@angular/common";
import { DataServiceService } from "../../data-service.service";
@Component({
  selector: "app-analytics",
  templateUrl: "./analytics.component.html",
  styleUrls: ["./analytics.component.scss"]
})
export class AnalyticsComponent implements OnInit {
  students: any;
  subjects: any;
  classes: any;
  overlay: boolean = false;
  subtopic_results:any;
  settings = {
    actions: false,
    hideSubHeader: true,

    columns: {

      name: {
        title: "Student Name",
        type: "string"
      },
      school_code: {
        title: "School Code",
        type: "string"
      },

      quiz_count: {
        title: "Questions Done",
      },
      average_grade: {
        title: "Average Score"
      },
      updated_at: {
        title: "Last Login",

      }
    }
  };
  subtopic_settings = {
    actions: false,
    hideSubHeader: true,

    columns: {
      subtopic: {
        title: "Subtopic",
        type: "string"
      },
      attempt_number: {
        title: "Attempt Number",
        type: "string"
      },
      score: {
        title: "Score",
      },
      class: {
        title: "Class"
      },
      created_at: {
        title: "Attempt Date",

      }
    }
  };
  constructor(private data_service: DataServiceService,private datePipe: DatePipe) {}


  ngOnInit() {
    this.getClasses();
    this.getSubjects();
    this.getStudents(1);
  }
  getSubjects() {
    this.data_service.getSubjects().subscribe(response => {
      console.log(response);
      this.subjects = response;
    });
  }
  getStudents(subject_id) {
    this.data_service.getStudents(subject_id).subscribe(response => {
      console.log(response);
      this.students = response;
    });
  }
  getClasses(){
    this.data_service.getClasses().subscribe(response=> {
      console.log(response);
      this.classes = response;
    })
  }

  onRowSelect(e){
    console.log(e);
    let id = e.data.id;

    this.data_service.getStudentSubtopicResults(id).subscribe(response => {
      this.overlay = true;
      this.subtopic_results = response;
      console.log(response);
    })
  }
  closeDetails(){
      this.overlay = false;
  }
}
